<template>
  <v-container class="d-flex justify-center align-center" fill-height>
    <v-card class="pa-5" elevation="10">
      <v-card-title class="text-h5 font-weight-bold">
        
      </v-card-title>

      <v-form v-model="valid" ref="form" @submit.prevent="login">
        <v-card-text>
          <!--  -->
          <v-text-field
            v-model="username"
            :rules="usernameRules"
            label=""
            outlined
            required
          ></v-text-field>
          <!--  -->
          <v-text-field
            v-model="password"
            :rules="passwordRules"
            label=""
            outlined
            type="password"
            required
          ></v-text-field>
        </v-card-text>

        <v-card-actions>
          <v-btn color="primary" type="submit" class="font-weight-bold"
            :loading="loading"
            :disabled="loading">
            
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-container>
</template>

<script>
export default {
  data() {
    return {

      //#region ()
      username: "admin",
      password: "mirdcUC",
      //#endregion

      //#region ()
      valid: false,
      usernameRules: [
        (v) => !!v || "",
        (v) => v.length >= 3 || "3",
      ],
      passwordRules: [
        (v) => !!v || "",
        (v) => v.length >= 6 || "6",
      ],
      //#endregion

      loading: false,

    };
  },
  methods: {
    async login() {
      // (Vuetify)      
      if(!this.$refs.form.validate()){
        this.$swal.fire("", "", "warning")
        return
      }

      //  API
      this.loading = true
      const formData = new FormData()
      formData.append("username", this.username)
      formData.append("password", this.password)
      await this.$store.dispatch("auth/login", formData) // 
      this.loading = false




    },
  },
};
</script>

<style scoped>
.v-card {
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.v-btn {
  width: 100%;
}
</style>
